import React, { useState, useEffect } from "react";
import axios from "axios";
import Button from "@material-ui/core/Button";

import Box from "@material-ui/core/Box";
import "./App.css";

function App() {
	const [quote, setQuote] = useState();

	const [click, setClick] = useState(0);

	useEffect(() => {
		fetch(`/api/quotes/random`)
			.then((res) => res.json())
			.then((data) => {
				setQuote(data.data);
				console.log(quote.content);
			})
			.catch(() => {
				console.log("err");
			});
	}, [click]);

	return (
		<div>
			<Box className="box" component="span" m={1}>
				<h1 className="logo">The Office</h1>
				<p className="quote">{`"${quote.content}"`}</p>
				<p className="worker">{`${quote.character.firstname} ${quote.character.lastname} `}</p>

				<Button onClick={() => setClick(click + 1)} variant="contained">
					<p className="btn">Dalje</p>
				</Button>
			</Box>
		</div>
	);
}

export default App;
